@import "../../scss/_vars.scss";

.Search {
    // stylin'
    position: relative;
    z-index: 9999;
    color: $black;
    //display: flex;
    box-shadow: 0px 22px 66px 12px rgba(#000, 0.15);
    padding: $spacer/2;
    user-select: none;
    pointer-events: none;
    input, .Search--box {
        padding: 0 $spacer/2;
        width: 100%;
        height: 88px;
        //max-width: 860px;
        font-size: 24px;
        line-height: 88px;
        font-weight: 600;
        border: 2px solid $green;
        border-radius: 4px;
        background: $white;
        white-space: nowrap;
        //box-shadow: 0px 22px 66px 12px rgba(#000, 0.15);
        @media screen and (max-width: 900px) {
            font-size: 18px;
        }
        span {
           position: relative;
           width: 30px;
           &:after {
               content: "";
               position: absolute;
               bottom: 2px;
               display: inline-block;
               width: 20px;
               border-bottom: 3px solid $black__light;
               animation: cursor;
               animation-duration: 1s;
               animation-iteration-count: infinite;
               animation-timing-function: ease-in-out;
           }
       }
       i {
           color: $black__light;
           font-weight: 400;
           padding-right: 8px;
       }
    }
    button {
        position: absolute;
        top: 38px;
        right: 38px;
        font-size: 30px;
        line-height: 60px;
        width: 60px;
        display: block;
        font-family: "Material Icons";
        border: none;
        background: $green;
        color: $white;
        border-radius: 4px;
       // box-shadow: 0px 22px 66px 12px rgba(#000, 0.15);
    }
}

@keyframes cursor {
    50% {
        opacity: 0;
    }
}

main.search {
    //display: flex;
    align-items: center;
    justify-content: center;
    
    .Search {
        position: absolute;
        width: 60vw;
        left: calc(50% - 30vw);
        .Search--box {
            width: 100%;
        }
        opacity: 0;
        animation: show;
        animation-duration: 500ms;
        animation-iteration-count: 1;
        animation-timing-function: ease-in-out;
        animation-fill-mode: forwards;
        animation-delay: 2s;
    }
}

@keyframes show {
    from {
        opacity: 0;
        transform: scale(0.5) translateY(50%);
    }
    to {
        opacity: 1;
        transform: scale(1) translateY(0%);
    }
}