@import "../../scss/_vars.scss";

.Banner {
    position: fixed;
    z-index: 99999;
    top: $spacer;
    left: calc(50vw - 20vw);
    width: 40vw;
    height: 88px;
    padding: 0 $spacer/2;
    display: flex;
    align-items: center;
    justify-content: center;
    background: $white;
    font-size: 22px;
    line-height: 28px;
    color: $black__darkest;
    text-align: center;
    box-shadow: 0px 20px 44px 8px rgba(#000, 0.24);
    border-radius: 6px;
    transition: all 500ms ease-in-out;
    user-select: none;
    
/*
    opacity: 0;
    animation: banner;
*/
    animation-fill-mode: forwards;
    animation-iteration-count: 1;
    animation-timing-function: ease-in-out;
    animation-duration: 500ms;
    animation-delay: 500ms;
    @media screen and (max-width: 900px) {
        font-size: 18px;
    }
    
}
.Banner > span {
    display: flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
    pointer-events: none;
}
.Banner > span b {
    position: relative;
    color: $blue;
    font-weight: 600;
    font-size: 14px;
    text-transform: uppercase;
    margin-left: $spacer/5;
    &:after {
        content: "";
        position: absolute;
        z-index: -1;
        top: calc(50% - 6px);
        left: calc(50% - 6px);
        width: 12px;
        height: 12px;
        background: crimson;
        border-radius: 100%;
        animation: flasher;
        animation-iteration-count: infinite;
        animation-timing-function: ease-out;
        animation-duration: 1s;
    }
}
@keyframes flasher {
    from {
        opacity: 1;
        transform: scale(0);
    }
    to {
        opacity: 0;
        transform: scale(4);
    }
}
.Banner.init, .Banner.done {
    background: transparent;
    box-shadow: 0px 0px 0px 0px rgba(#000, 0);
}
.Banner.board {
    top: calc(50vh - 72px);
    left: calc(50vw - 350px);
    width: 700px;
    height: 144px;
    font-size: 28px;
    font-weight: 600;
    box-shadow: 0px 34px 66px 8px rgba(#000, 0.3);
    background: rgba($yellow, 0.95);
    //box-shadow: 0px 0px 0px 0px rgba(#000, 0);
    @media screen and (max-width: 980px) {
        left: calc(50vw - 30vw);
        width: 60vw;
        top: calc(50vh - 50px);
        height: 100px;
        font-size: 18px;
    }
}
.Banner.stack {
    top: 68%;   
    background: transparent;
    box-shadow: 0px 0px 0px 0px rgba(#000, 0); 
}
.Banner.battlecard {
    top: 80%;
    left: calc(50vw - 290px);
    width: 580px;
    background: rgba($yellow, 0.95);
    box-shadow: 0px -20px 44px 8px rgba(#000, 0.24);
    font-weight: 600;
    @media screen and (max-width: 900px) {
/*
        background: none;
        box-shadow: none;
        color: $black;
*/
    }
}
.Banner.search {
    top: 30%;   
    background: transparent;width: 100%;
    left: 0;
    font-size: 30px;
    box-shadow: 0px 0px 0px 0px rgba(#000, 0); 
}
.Banner.done {
    top: calc(50vh - 44px); 
}
.Banner.done + .Cards {
    opacity: 0;
}
@keyframes banner {
    from {
        opacity: 0
    }
    to {
        opacity: 1;
    }
}