@import "../../scss/_vars.scss";

$gutter : 4px;
$factor : 7;
$width : calc((100% / #{$factor}) - 4px);

.Cards {
    // stylin'
    position: absolute;
    top: 0px;
    left: 0;
    width: 100%;
    height: 100vh;
    pointer-events: none;
    transition: all 1s ease-in-out;
    //transition-delay: 200ms;
    //cursor: none;
    .Card {
        position: absolute;
        //cursor: pointer;
        //cursor: none;
        width: calc(100%/#{$factor});
        padding: 1px;
/*
        transform-origin: 50% 50%;
        perspective: 1200px;
*/
        $bezier : cubic-bezier(0.175, 0.885, 0.32, 1.275);
        transition: all 550ms ease-in-out, opacity 400ms ease-in-out;
        
        //opacity: 0;
        img {
            //margin-bottom: -6px;
            position: relative;
            display: block;
            margin-bottom: 1px;
        }
        @for $i from 1 through 100 {
            &:nth-child(#{$i}) {
                transition-delay: #{($i * 35) + 750}ms;
            }
        }
        &.off {
            opacity: 0;
            transform: scale(0.5);
        }
     }
}
.Cards.init, /* .Cards.search */ {
    opacity: 0;
    transform: scale(0.5);
    visibility: hidden;
}

.Cards.board {
/*
    opacity: 0;
    animation: show;
    animation-delay: 2s;
    animation-duration: 500ms;
    animation-iteration-count: 1;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
*/
}
.Cards.search {
   // transform: translateY(20%);
/*
    opacity: 0;
    animation: show;
    animation-duration: 500ms;
    animation-iteration-count: 1;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
*/
    .Card {
        opacity: 0;
        animation: searchCard;
        animation-duration: 15s;
        animation-delay: 2s;
        animation-iteration-count: infinite;
        animation-timing-function: ease-in-out;
        //animation-fill-mode: forwards;
        @for $i from 1 through 100 {
            &:nth-child(#{$i}) {
                z-index: #{$i};
                animation-duration: #{($i * 2000)}ms;
                
            }
        }
        &:nth-child(3n) {
            filter: blur(4px);
        }
    }
}
@keyframes searchCard {
    0% {
        opacity: 0;
        transform: translateY(200%);
    }
    50% {
        opacity: 0.5;
        //transform: scale(1);
    }
    75% {
        opacity: 0.25;
        //transform: scale(1);
    }
    100% {
        opacity: 0;
        transform: translateY(-200%);
    }
}
.Cards.done {
    .Card {
        display: none;
    }
}