@import "../../scss/_vars.scss";
* {
    cursor: none !important;
}
.Cursor {
    // stylin'
    position: absolute;
    z-index: 9999999;
    top: calc(50% - 22px);
    left: calc(50% - 22px);
    width: 44px;
    height: 44px;
    border-radius: 100%;
    background: crimson;
    opacity: 0.85;
    transform: scale(0.35);
    pointer-events: none;
    border: 2px solid transparent;
    transition: transform 130ms ease-in-out, opacity 260ms ease-in-out;
    &.hide {
        opacity: 0;
    }
    &.on {
        background: rgba($blue, 0.15);    
        border-color: $blue;
        opacity: 0.5;
        animation: jump;
        animation-duration: 130ms;
        animation-delay: 200ms;
        animation-fill-mode: forwards;
        animation-iteration-count: 1;
        animation-timing-function: ease-in-out;
    }
}
@keyframes jump {
    50% {
        transform: scale(2);
    }
    100% {
        transform: scale(1.5);
    }
}