@import "../../scss/_vars.scss";

.Tiles {
    display: grid;
    grid-template-columns: repeat(5, 13vw);
    grid-gap: 2px;
    pointer-events: none;
    .Tile {
        position: relative;
        max-width: 100%;
        padding: $spacer/4;
        background: $white;
        height: 13vw;
        max-height: 216px;
        border: 3px solid $tan-darker;
        display: grid;
        justify-content: center;
        align-content: center;
        text-align: center;
        opacity: 0.4;
        transition: all 320ms ease-in-out;
        
        > div {
            transition: all 320ms ease-in-out;
        }
        img {
            width: 4vw;
            height: 4vw;
            max-width: 68px;
            max-height: 68px;
            object-fit: contain;
            margin-bottom: $spacer/4;
            border-radius: 4px;
            @media screen and (max-width: 900px) {
                margin-bottom: $spacer/8;
            }
        }
        h4 {
            font-size: 18px;
            font-weight: 600;
            @media screen and (max-width: 900px) {
                font-size: 14px;
            }
        }
        &:nth-child(8) {
            z-index: 9999;
            opacity: 1;
            box-shadow: 0px 0px 44px 4px rgba(#000, 0.24);
        }
        
        animation-fill-mode: forwards;
        animation-duration: 500ms;
        animation-iteration-count: 1;
        animation-timing-function: ease-in-out;
        @for $i from 1 through 100 {
            &:nth-child(#{$i}) {
                animation-delay: #{($i * 35) + 20}ms;
            }
        }
    }
}
// fade out the Tiles when Lanes are next
main:not(.init) .Tiles, .done .Tiles {
    pointer-events: none;
    .Tile { //:not(:nth-child(8)) {
        animation-name: TilesOut;
    }
    .Tile:nth-child(8)  {
        animation-duration: 2s;
/*
        transition-duration: 1s;
        transition-delay: 1s;
        transform: scale(18);
        opacity: 0;
        border-color: $white;
        > * {
            opacity: 0;
            transition-delay: 1s;
        }
*/
    }
}

@keyframes TilesOut {
    0% {
        opacity: 0.4;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
        transform: scale(1) translateX(-800%);
    }
}