
@import "./scss/_vars.scss";


// opinionated defaults
html {
    box-sizing: border-box;
    font-size: 16px;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body, h1, h2, h3, h4, h5, h6, p, ol, ul {
    margin: 0;
    padding: 0;
    font-weight: normal;
}
ol, ul {
    list-style: none;
}

img {
    max-width: 100%;
    height: auto;
    margin-bottom: -3px;
}

// my shit
body {
    background: $tan;
    color: $black;
    font-family: $font;
    font-size: 14px;
    line-height: 20px;
    width: 100vw;
}

main {
    position: absolute;
    height: 100vh;
    width: 100vw;
    display: grid;
    justify-content: center;
    align-content: center;
    overflow: hidden;
    //cursor: none;

}
